import React, { Component } from 'react'
import Todoinput from './Todoinput'
import Todoing from './Todoing'

export default class Todolist extends Component {
    constructor(){
        super();
        this.state = {
            todo:[],
        }
        this.state.todo = JSON.parse(localStorage.getItem("todo"));
    }
    addItem = (data)=>{
        
        if(this.state.todo == null){
            this.state.todo = [{
                title : data,
                done : "false"
            }]
        }else{
            this.state.todo.push({
                title : data,
                done : "false"
            })
        }
        this.setState({
            todo : this.state.todo
        },()=>{
            localStorage.setItem("todo",JSON.stringify(this.state.todo));
        })
        
    }
    delItem = (idx)=>{
        this.setState((state)=>{
            return {
                todo: state.todo.filter((item,index)=>idx!=index)
            }
        },()=>{
            localStorage.setItem("todo",JSON.stringify(this.state.todo));
        })

    }
    checkItem = (e,idx)=>{
        console.log(e.target.checked);
        if(e.target.checked == true){
            this.state.todo[idx].done = 'true';
        }else{
            this.state.todo[idx].done = 'false';
        }
        this.setState({
            todo : this.state.todo
        },()=>{
            localStorage.setItem("todo",JSON.stringify(this.state.todo));
        })
    }
    render() {
        console.log(JSON.parse(localStorage.getItem("todo")));
        return (
            <div>
                <Todoinput add={this.addItem}/>
                <Todoing del={this.delItem} check={this.checkItem} todo={this.state.todo}/>
            </div>
        )
    }
}