import React, { Component } from 'react'

export default class Todoing extends Component {


    render() {
        var {todo} = this.props;
        if(todo == null){
            return (
                <div>
                    <h2>Doing                     <span>0</span></h2>
                    <ul></ul>
                    <h2>Success</h2>
                    <ul></ul>
                </div>
            )
        }else{
            return (
                <div>
                    <h2>Doing                     <span>{todo.length}</span></h2>
                    <ul>
                        {
                            todo.map((item,idx)=>{
                                console.log(item.done);
                                if(item.done === 'false'){
                                    return <li key={idx}>
                                                <input type='checkbox' name='check' onClick={(e)=>this.props.check(e,idx)}/>
                                                    {item.title}
                                                <button onClick={()=>this.props.del(idx)}>
                                                    del
                                                </button>
                                            </li>
                                }
                            })
                        }
                    </ul>
                    <h2>Success</h2>
                    <ul>
                        {
                           todo.map((item,idx)=>{
                                if(item.done == 'true'){
                                    return <li key={idx}>
                                                <input type='checkbox' name='check' onClick={(e)=>this.props.check(e,idx)} checked='checked'/>
                                                    {item.title}
                                                <button onClick={()=>this.props.del(idx)}>
                                                    del
                                                </button>
                                            </li>
                                }
                            })
                        }
                    </ul>
                </div>
            )
        }
        
    }
}  